"use strict";
class HTTPError extends Error {
    constructor(status, msg) {
        super(msg);
        this.message = msg;
        this.status = status;
    }
}
exports.HTTPError = HTTPError;
class ConnectionBrokenError extends HTTPError {
    constructor() {
        super(0, 'Connection to server broken');
    }
}
exports.ConnectionBrokenError = ConnectionBrokenError;
class AuthenticationRequiredError extends HTTPError {
    constructor(data) {
        super(401, 'Authentication required');
        Object.assign(this, data);
    }
}
exports.AuthenticationRequiredError = AuthenticationRequiredError;



